import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';

/**
 * Generated class for the HemstopPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-hemstop',
  templateUrl: 'hemstop.html',
})
export class HemstopPage {

  isShownHEMSTOP:boolean=false;
  totalHEMSTOP:number;
  risqueHEMSTOP:string="Rpondre aux questions du score.";

  public HEMSTOPform = [
    {  val: "Q1. Avez-vous dj consult un mdecin ou reu un traitement pour un saignement prolong ou inhabituel par exemple un saignement de nez ou une petite coupure ?" , count: 0 },
    {  val: "Q2. Avez-vous tendance  faire des bleus de plus de 2 cm ou des hmatomes importants, sans choc ou traumatisme ou bien  un traumatisme minime ?" , count : 0 },
    {  val: "Q3. Avez-vous reconsult votre dentiste pour un saignement aprs une extraction dentaire ?", count : 0 },
    {  val: "Q4. Avez-vous saign de manire anormale aprs une intervention chirurgicale (exemple opratoire des amygdales ou circoncision) ?", count : 0 },
    {  val: "Q5. Y a-t-il des membres de la famille proche suivis pour une maladie de la coagulation, comme la maladie de Von Willebrand ou l'hmophilie ?", count : 0 },
    {  val: "Q6. Pour les femmes : avez vous dj consult un mdecin ou reu un traitement pour des rgles trop abondantes ? Avez vous saign de faon anormale aprs un accouchement ?", count : 0 },
  ];

  constructor(public navCtrl: NavController, public navParams: NavParams) {
  }

  retourHome(){
    this.navCtrl.pop();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad HemstopPage');
  }

  
  countHEMSTOP(){
    this.totalHEMSTOP =  Math.floor(this.HEMSTOPform[0].count) + 
                          Math.floor(this.HEMSTOPform[1].count) +
                          Math.floor(this.HEMSTOPform[2].count) + 
                          Math.floor(this.HEMSTOPform[3].count) + 
                          Math.floor(this.HEMSTOPform[4].count) + 
                          Math.floor(this.HEMSTOPform[5].count) ; 
    console.log(this.totalHEMSTOP); 
    
    if (this.totalHEMSTOP >= 2){this.risqueHEMSTOP = "Forte probabilit de sur-risque hmorragique. Bilan d'hmostase recommand.";}
    else if (this.totalHEMSTOP == 1){this.risqueHEMSTOP = "Probabilit intermdiaire de sur-risque hmorragique. Bilan d'hmostase  discuter."}
    else {this.risqueHEMSTOP = "Probabilit trs faible de sur-risque hmorragique. Pas de bilan d'hmostase."}
  
  };

}



