import { Component } from '@angular/core';
import { NavController, NavParams, ToastController } from 'ionic-angular';
import { Storage } from '@ionic/storage';

/**
 * Generated class for the ChirurgieOrthognatiquePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-chirurgie-orthognatique',
  templateUrl: 'chirurgie-orthognatique.html',
})
export class ChirurgieOrthognatiquePage {


  public Dispositifs = <any> [
    { title: "Intubation nasotrachale", item:"" , isChecked: false, },
    { title: "Prvention", item: "Sonde naso-gastrique systmatique. Sonde thermique rectale et rchauffement cutan externe. Packing endobuccal." , isChecked: false, },
    ];

public PerOperatoire = <any> [
  { title: "Entretien par halogns", item:  "Objectif de PAM adapt  l'ge (65 mmHg pour les adolescents).", isChecked: false },
  { title: "Analgsie per-opratoire", item: "Sufentanil en rinjections par bolus.", isChecked: false },
  { title: "Exacyl", item:"10 mg/kg sur 30 minutes, dbut 20 minutes avant l'incision, puis 5 mg/kg/h en continu en per-opratoire, sans dpasser 4g/24h." , isChecked: false},
  { title : "Antibioprophylaxie" , item : "Augmentin 50 mg/kg. Dalacine 15 mg/kg et Flagyl 20 mg/kg en cas d'allergie aux bta-lactamines." , isChecked : false},
  { title : "Prvention douleur & NVPO" , item : "Dexamthasone 0,15 mg/kg et Paractamol 15 mg/kg en systmatique. Discuter Acupan et Ondansetron en fin de chirurgie en fonction des facteurs de risque (cf. score VPOP).", isChecked : false},
  { title : "Anesthsie loco-rgionale" , item : "Proposer une ALR de la face en fin de chirurgie : bloc du V-2 pour les ostotomies maxillaires, bloc du V-3 pour les OSBM."}   
];

public PostOperatoire = <any> [
  { title: "Antibioprophylaxie", item: "Poursuivie 48h en accord avec les recommandations SFAR : Augmentin 25 mg/kg/8h.", isChecked: false },
  { title : "Antalgiques et anti-mtiques", item:"Analgsie multimodale comprenant glace sur le visage et antalgiques intraveineux : Paractamol, Acupan, Tramadol. Proposer l'Ondansetron en premire intention dans le traitement des NVPO ; le Droleptan en rescue."},
  { title: "Scurit du patient", item: "Paire de ciseaux scotche sur le lit si blocage.", isChecked: false },
  { title: "Lutte contre l'inflammation", item:"Methylprednisolone 1 mg/kg/j pendant 3 jours : 24h IV puis 48h PO.", isChecked : false},
  { title : "Rhabilitation prcoce", item : "Position demi-assise ds la SSPI. Ablation de la SNG aprs vidange gastrique  J1 puis alimentation orale mixe. Sortie  J1 en l'absence de contre-indication."}
];


isShownDispositifs:boolean=false; 
isShownPostOp:boolean=false; 
isShownPerOp:boolean=false;

allCheckedDispositifs:boolean=false;
allCheckedPerOp:boolean=false;
allCheckedPostOp:boolean=false;

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public storage: Storage,
    public ToastController: ToastController, 
    ) {
  }


  ionViewDidLoad() {
    console.log('ionViewDidLoad ChirurgieOrthognatiquePage');
  };

  retourHome(){
    this.navCtrl.pop();
  };

  ToggleDispositifs() {
    this.isShownDispositifs = !this.isShownDispositifs;
  };

  TogglePerOp() {
    this.isShownPerOp = !this.isShownPerOp; 
  };

  TogglePostOp() {
    this.isShownPostOp = !this.isShownPostOp;
  };

  checkDispositifs(){

    let checkPreOp = this.Dispositifs
        .filter(item => item.isChecked === false)
        .reduce((sum, current) => sum + current.count, 0);
    
    if (checkPreOp == 0){
    this.allCheckedDispositifs = true;
    const toastDispositifs = this.ToastController.create({
      message: "Items dispostifs tous valids.",
      position: 'Bottom',
      showCloseButton: true,
      closeButtonText: "OK.",
      duration:3000, 
      });
      toastDispositifs.present();
      }
    else {this.allCheckedDispositifs = false;};
     
      };
    
      checkPerOperatoire() {
    
        let checkPerOp = this.PerOperatoire
        .filter(item => item.isChecked === false)
        .reduce((sum, current) => sum + current.count, 0);
    
    if (checkPerOp == 0){
    this.allCheckedPerOp = true;
    const toastPerOp = this.ToastController.create({
      message: "Items spcifiques du per-opratoire tous valids.",
      position: 'Bottom',
      showCloseButton: true,
      closeButtonText: "OK.",
      duration:3000, 
      });
      toastPerOp.present();
      }
    else {this.allCheckedPerOp = false;};
    
      }; 
    
      checkPostOperatoire() {
    
    let checkPostOp = this.PostOperatoire
      .filter(item => item.isChecked === false)
      .reduce((sum, current) => sum + current.count, 0);
    
    if (checkPostOp == 0){
    this.allCheckedPostOp = true;
    const toastPostOp = this.ToastController.create({
      message: "Items spcifiques du post-opratoire tous valids.",
      position: 'Bottom',
      showCloseButton: true,
      closeButtonText: "OK.",
      duration:3000, 
      });
      toastPostOp.present();
      }
    else {this.allCheckedPostOp = false;};
    
      };
    

}



